<template>
  <v-container :class="{'pa-3':$vuetify.breakpoint.xsOnly}">
    <v-layout>
      <v-flex>
        <v-form ref="form" lazy-validation>
          <v-text-field name="userId" ref="userId" height="20" v-model="userId" :error="userIdError" :rules="userIdRules" label="아이디" required maxlength="50" @focus="clearError" autofocus @keydown.enter.stop="focusPassword"></v-text-field>
          <v-text-field name="password" ref="password" height="20" v-model="password" :error="passwordError" :rules="passwordRules" label="비밀번호" required type="password" @keydown.enter.stop="signin" @focus="clearError"></v-text-field>

          <v-btn color="primary" @click="signin" block :loading="loading">로그인</v-btn>
          <v-layout row justify-space-between class="mt-3">
            <v-flex>
              <v-checkbox class="small" v-model="rememberMe" label="자동 로그인" hide-details></v-checkbox>
            </v-flex>
            <v-flex text-xs-right>
              <router-link to="/resetPassword">비밀번호 찾기</router-link>
            </v-flex>
          </v-layout>
          <p class="text-xs-center" v-show="message">
            <span class="error--text">{{message}}</span>
          </p>
          <p :class="{'text-xs-center':true, 'mt-3':!message}">
            <component :is="$vuetify.breakpoint.xsOnly?'span':'small'">
              <router-link to="/signup">아직 회원이 아니신가요?</router-link>
            </component>
          </p>
        </v-form>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import jwt from "jwt-decode";

function getCookie(cname) {
  let name = cname + "=";
  let decodedCookie = decodeURIComponent(document.cookie);
  let ca = decodedCookie.split(";");
  for (let i = 0; i < ca.length; i++) {
    let c = ca[i];
    while (c.charAt(0) === " ") {
      c = c.substring(1);
    }
    if (c.indexOf(name) === 0) {
      return c.substring(name.length, c.length);
    }
  }
  return "";
}

function deleteCookie(name) {
  document.cookie = name + "=; expires=Thu, 01 Jan 1970 00:00:01 GMT;";
}

function setCookie(name, value, days) {
  let expires = "";
  if (days) {
    let date = new Date();
    date.setTime(date.getTime() + days * 24 * 60 * 60 * 1000);
    expires = "; expires=" + date.toUTCString();
  }
  document.cookie = name + "=" + (value || "") + expires + "; path=/";
}
export default {
  name: "Signin",
  data: () => ({
    loading: false,
    message: null,
    userIdError: false,
    passwordError: false,
    userId: "",
    password: "",
    rememberMe: false,
    userIdRules: [v => !!v || "아이디를 입력해주세요."],
    passwordRules: [v => !!v || "비밀번호를 입력해주세요."]
  }),
  created() {
    const token = getCookie("token");
    if (token) {
      this.loading = true;
      this.$axios({
        method: "POST",
        url: "/refresh",
        headers: {silent: true}
      })
        .then(response => {
          this.loading = false;
          this.$store.dispatch("setUserId", jwt(response.data.token).userId);

          const redirectTo = response.data.redirectTo;
          if (response.data.imminent || response.data.needEmail) {
            this.$store.dispatch("updateAuthInformation", {imminent: response.data.imminent, needEmail: response.data.needEmail});
          }

          if (redirectTo) {
            if (redirectTo === "/auth" && getCookie("authRequirement") && getCookie("authRequirement") >= this.$moment().format("YMMDD")) {
              this.$router.push(decodeURIComponent(window.location.search.replace(new RegExp("^(?:.*[&\\?]" + encodeURIComponent("redirectTo").replace(/[.+*]/g, "\\$&") + "(?:\\=([^&]*))?)?.*$", "i"), "$1")) || "/");
            } else {
              this.$router.push(redirectTo + window.location.search); //preserve original redirect options
            }
          } else {
            const searchRedirectTo = decodeURIComponent(window.location.search.replace(new RegExp("^(?:.*[&\\?]" + encodeURIComponent("redirectTo").replace(/[.+*]/g, "\\$&") + "(?:\\=([^&]*))?)?.*$", "i"), "$1"));
            this.$router.push(searchRedirectTo !== "/index" && searchRedirectTo !== "/signin" && searchRedirectTo !== "" ? searchRedirectTo : "/");
          }
        })
        .catch(err => {
          this.loading = false;
          if (err.response && err.response.data) {
            this.message = err.response.data.message;
          } else {
            this.message = "서버에 접속할 수 없습니다. 인터넷 연결을 확인해주세요.";
          }
          deleteCookie("token");
        });
    } else if (getCookie("userId")) {
      this.userId = getCookie("userId");
    }
  },

  watch: {
    userId() {
      if (this.userIdError) {
        this.userIdError = false;
      }
      if (this.message) {
        this.message = null;
      }
    },
    password() {
      if (this.passwordError) {
        this.passwordError = false;
      }
      if (this.message) {
        this.message = null;
      }
    }
  },

  methods: {
    clearError() {
      this.$refs.form.resetValidation();
      this.message = null;
    },
    signin() {
      if (this.$refs.form.validate()) {
        if (!this.userId || !this.password || this.userId === "" || this.password === "") {
          this.message = "아이디와 비밀번호를 입력해주세요.";
          return false;
        }
        setCookie("userId", this.userId, 30);
        this.message = null;
        this.userIdError = false;
        this.passwordError = false;
        this.loading = true;
        this.$axios
          .post(
            "/signin",
            {
              userId: this.userId,
              password: this.password,
              rememberMe: this.rememberMe
            },
            {headers: {silent: true}}
          )
          .then(response => {
            this.$store.dispatch("setUserId", this.userId);
            const redirectTo = response.data.redirectTo;
            if (response.data.imminent || response.data.needEmail) {
              this.$store.dispatch("updateAuthInformation", {imminent: response.data.imminent, needEmail: response.data.needEmail});
            }
            if (redirectTo) {
              if (redirectTo === "/auth" && localStorage.getItem("authRequirement") && localStorage.getItem("authRequirement") >= this.$moment().format("YMMDD")) {
                this.$router.push(decodeURIComponent(window.location.search.replace(new RegExp("^(?:.*[&\\?]" + encodeURIComponent("redirectTo").replace(/[.+*]/g, "\\$&") + "(?:\\=([^&]*))?)?.*$", "i"), "$1")) || "/");
              } else {
                this.$router.push(redirectTo + window.location.search); //preserve original redirect options
              }
            } else {
              const searchRedirectTo = decodeURIComponent(window.location.search.replace(new RegExp("^(?:.*[&\\?]" + encodeURIComponent("redirectTo").replace(/[.+*]/g, "\\$&") + "(?:\\=([^&]*))?)?.*$", "i"), "$1"));
              this.$router.push(searchRedirectTo !== "/index" && searchRedirectTo !== "/signin" && searchRedirectTo !== "" ? searchRedirectTo : "/");
            }
            this.loading = false;
          })
          .catch(err => {
            this.loading = false;
            if (err.response && err.response.data) {
              if (err.response.data.target && this.$refs[err.response.data.target]) {
                switch (err.response.data.target) {
                  case "userId":
                    this.userIdError = true;
                    this.$refs.userId.focus();
                    break;
                  case "password":
                    this.passwordError = true;
                    this.$refs.password.focus();
                    break;
                  default:
                    break;
                }
              }
              this.message = err.response.data.message;
            } else {
              this.message = "서버에 접속할 수 없습니다. 인터넷 연결을 확인해주세요.";
            }
          });
      }
    },
    focusPassword() {
      if (this.password !== "") {
        this.signin();
      } else {
        this.$refs.password.focus();
      }
    }
  }
};
</script>

<style>
.small i.v-icon {
  font-size: 18px;
}
.small label {
  font-size: 15px;
}
.small.v-input--checkbox {
  margin-top: 0;
  padding-top: 0;
}
.small .v-input--selection-controls__input {
  width: 16px;
  height: 16px;
  margin-right: 4px;
}
</style>
